.card {
  flex-basis: calc(25% - 20px);
  border-radius: 8px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  margin-bottom: 20px;
  text-align: center;
}

.header1 {
  background-color: #007bff;
  color: white;
  padding: 20px;
  text-align: center;
}

.body1 {
  padding: 20px;
  text-align: left;
}

h2 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 24px;
}

p {
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 14px;
}


