.Searchbar{
        border-radius: 20px;
        width: 60%;
        position: relative;
        margin:0 auto;
        
 }
 .search{
 border-radius: 15px;
 width: 100%;
 background-color: #070A52;
 padding:0.5rem;  
 color: #F0F0F0; 
 }
 .card-container{
    margin-left: 20%; 
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(3,25%);
 }
 .card1{
    background-color: #070A52;
  display:flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 20px;
  width: 90%;
  height: 150px;
  padding: 1rem;
  font-size: 0.9rem;
  margin-top: 20px;
  margin-right: 30px;
  color: #F0F0F0; 
 }
 input:focus{
    outline:none;
 }
 .delete{
  width:50%;
  background-color: #2F58CD;
  border-radius:1rem ;
  color:#F0F0F0 ;
 }
